.banner{
    color: white;
    object-fit:"contain";
    height: 448px;    
}



.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_description{
    width: 45rem;
    line-height: 1.3;
    padding-top:1rem ;
    font-size: 1.8rem;
    max-width: 360px;
    height: 80px;
}

.banner_buttons{
    display: flex;
}


.banner_button{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(132, 132, 132, 0.5);
}

.banner_button:hover{
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}

.banner_fadeBottom{
    margin-left: -30px;
    height: 13rem;
    background-image:linear-gradient(180deg,transparent,rgba(34,34,34,0.61),#111) ;
}